@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Changa:wght@200;300;400;500;600;700;800&family=Permanent+Marker&display=swap);
.nav{
  display: flex;
  min-width: 80%;
  justify-content: space-between;
  font-weight: 600;
  font-size: 25px;
  align-items: center;
  background-color: var(--primary);
  height: 80px;
  padding: 10px;
  color: var(--secondary);
  font-family: 'Changa', sans-serif;
  
}

.active_link{
  color: var(--third);
  background-color: var(--fourth);
  border-radius: 7px;
}


.nav a{
  width: 150px;
  text-align: center;
  -moz-transition: all 1s ease-out;
  -o-transition: all 1s ease-out;
  -webkit-transition: all 1s ease-out;
  margin-left: 15px;
  padding: 5px;
  }
  
.nav a:hover{
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -o-transform: scale(1.1);  
}
  
    nav.scrollmenu {
      white-space: nowrap;
      overflow: auto;
}
    
    nav.scrollmenu a {
      display: inline-block;
    
    }

    @media (max-width: 600px){
      .nav{
        font-size: 22px;
      }

      .nav a{
        width: 130px;
        text-align: center;
        margin-left: 15px;
        padding: 5px;
        }
}

    @media(max-width: 500px){
      .nav a:hover{
        cursor: pointer;
        -webkit-transform: scale(1.0);
        -moz-transform: scale(1.0);
        -o-transform: scale(1.0);  
      }
    }

.mapStat {
    width: 98vw;
    margin: auto;
    height: 63vh;
    
}


.statistics{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column; 
    position: absolute;
